import { Injectable } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NtechDialogboxComponent } from 'src/app/ntech-core-components/ntech-core-components/core-base/ntech-alert-popup/ntech-alert-popup.component';

@Injectable()
export class NtechDialogboxService {

  constructor(private modalService: NgbModal) { }

  public confirm(
    title: string,
    message: string,
    btnOkText: string = 'OK',
    btnCancelText: string = 'Cancel',
    dialogSize: 'sm'|'lg' = 'sm'): Promise<boolean> {
    
    const modalRef = this.modalService.open(NtechDialogboxComponent, { size: dialogSize });
   
    modalRef.componentInstance.title = title;
    modalRef.componentInstance.message = message;
    modalRef.componentInstance.btnOkText = btnOkText;
    modalRef.componentInstance.btnCancelText = btnCancelText;

    return modalRef.result;
  }

}
